<template>
  <div id="app">
    <!-- <el-image src="./assets/139.png" fit="contain" style="width:100px;height:80px"></el-image> -->
    <div class="page_one" id="one">
      <img src="./assets/139.png" alt="" class="logo">
      <div class="login-tip">
        <!-- <p>198****1498@139.com</p>
        <p>正在登陆中...</p> -->
      </div>
      <el-button type="primary" class="login-btn" round @click="login">一键登录</el-button>
    </div>
    <!-- <div class="authrize">
      <p>198****1498@139.com</p>
      <p>正在登陆中</p>
    </div> -->
    <!-- <div id="login">
      <img src="./assets/login-tip.jpg" alt="" id="login-tip">
    </div> -->
    <img src="./assets/login-tip.jpg" alt="" id="login-tip">
    <div class="page_two" id="two">
      <el-button type="danger" id="logout" @click="exittip">退出</el-button>
      <img src="./assets/home.jpg" alt="" class="home">
    </div>
  </div>
</template>

<script>
import { setTimeout } from 'timers';
export default {
  name: 'app',
  components: {
  },
  methods: {
    tip() {
      let one = document.querySelector('#one');
      let two = document.querySelector('#two');
      let login = document.querySelector('#login-tip');
      this.$confirm('"一键登录"功能可以免密码快速登录本机账号，需要获取用户手机号信息用于身份验证，同时还可能需要申请短信权限，是否继续', '权限申请', {
          confirmButtonText: '继续',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
         one.style.display = 'none';
         login.style.display = 'block'; 
        setTimeout(() => {
          one.style.display = 'none';
          two.style.display = 'block';
          login.style.display = 'none'; 
        },2000);
        }).catch(() => {

        });
    },
    login() {
      this.tip()
      // let one = document.querySelector('#one');
      // let two = document.querySelector('#two');
      // one.style.display = 'none';
      // setTimeout(() => {
      //   two.style.display = 'block';
      // },500)
    },
    exittip() {
      let one = document.querySelector('#one');
      let two = document.querySelector('#two');
      one.style.display = 'flex';
      two.style.display = 'none';
      // this.$alert('退出吗', {
      //   confirmButtonText: '退出',
      //   callback: () => {
      //     one.style.display = 'flex';
      //     two.style.display = 'none';
      //   }
      // })
      // one.style.display = 'flex';
      // setTimeout(() => {
      //   two.style.display = 'none';
      // },500)
    }
  }
}
</script>

<style> 
document {
  background-color: aqua
}
#app {
  width: 100%;
  height: 100%;
}
.page_one {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.page_two {
  display: none;
}
.logo {
  width: 80%;
  height: 40%;
  margin-bottom: 50px;
}
.login-btn {
  width: 100%;
  font-size: 20px;
}
#login-tip {
  display: none;
  width: 100%;
  height: 100%;
}
.home {
  width: 100%;
  height: 100%;
}
#logout {
  position: fixed;
  margin-left: 50%;
  transform: translateX(-50%)
}
</style>



